import { createWebsiteChannel } from './channel';
import type { CreateWebsiteHost, ClientSDK } from './types';

declare global {
  interface Window {
    clientSdk?: ClientSDK;
  }
}

declare const $wixContext: {
  clientSdk: ClientSDK;
};

export const createHost: CreateWebsiteHost = function (config) {
  const clientSdk =
    (typeof $wixContext !== 'undefined' && $wixContext.clientSdk) ||
    config?.clientSdk ||
    window.clientSdk;
  const { applicationId } = config || {};

  if (!applicationId) {
    throw new Error(
      '"createHost" was called without a required field "applicationId"',
    );
  }

  return {
    // environment: {},
    channel: createWebsiteChannel({ clientSdk, applicationId }),
    close: () => {},
  };
};
